body {
  background: -webkit-gradient(linear, left top, right top, from(black), to(navy));
  background: -webkit-linear-gradient(left, black, navy);
  background: -o-linear-gradient(left, black, navy);
  background: linear-gradient(90deg, black, navy);
  height: 100%;
}

.App {
  text-align: center;
  font-size: 25px;
  margin-top: 20%;
}

.App-title {
  font-size: 45px;
  margin: 20px;
}

.clock-days, .clock-hours, .clock-mins, .clock-secs {
  display: inline;
  margin: 5px;
}

.deadline-input {
  font-size: 25px;
  margin: 5px;
}

.countdown {
  margin: 20px 0px 30px 0px;
}

.btn {
  font-size: 16px;
}

div {
  color: white;
}



